@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

$primary-color: #6db784;
$white-color: #fff;
$blue-color: #1b4d89;
$red-color: #e64e53;
$yellow-color: #f9e45b;

.bg-green {
  background-color: $primary-color;
}
.bg-white {
  background-color: $white-color;
}
.bg-blue {
  background-color: $blue-color;
}
.bg-red {
  background-color: $red-color;
}
.bg-yellow {
  background-color: $yellow-color;
}

.font-green {
  color: $primary-color;
}

.font-blue {
  color: $blue-color;
}

.font-red {
  color: $red-color;
}

.font-white {
  color: $white-color;
}

.align-center {
  text-align: center;
}

.h1 {
  font-size: 42px;
}

.h2 {
  font-size: 36px;
  padding: 36px 0;
}
.h3 {
  font-size: 26px;
  padding: 26px 0;
}
